export default {
  data: function () {
    return {
      data: "",
    };
  },
  components: {},

  methods: {
    check_opening_entry() {
      const vm = this;
      frappe.call({
        method: "csf_tz.csf_tz.page.jobcards.jobcards.get_job_cards",
        args: {},
        async: true,
        callback: function (r) {
          if (r.message) {
            console.log(r.message);
            vm.data = r.message;
          }
        },
      });
    },
  },
  created: function () {
    this.check_opening_entry();
  },
};
